<template>
  <el-select
    class="wl-select"
    ref="wl-vue-select"
    :value="value"
    :size="size"
    :multiple="multiple"
    :disabled="disabled"
    :value-key="valueKey"
    :clearable="clearable"
    :filterable="filterable"
    :placeholder="placeholder"
    :allowCreate="allowCreate"
    :multipleLimit="multipleLimit"
    @change="selectChange"
  >
    <el-option
      label=""
      key="select-all"
      :value="
        valueKey
          ? { [selfProps.value]: empty, [selfProps.label]: '' }
          : empty
      "
      v-if="multiple && selfData.length > showTotal"
    ></el-option>
    <el-option
      v-for="item in selfData"
      :key="item[selfProps.value]"
      :label="item[selfProps.label]"
      :value="valueKey ? item : item[selfProps.value]"
    ></el-option>
  </el-select>
</template>

<script>
/**
 * auth:weilan
 * github: https://github.com/hql7
 * description: el-select
 * props:
 *    data->
 *    props-> label:"label", value: "value"
 *    showTotal-> ,multiple
 *    value-> v-model
 *    others->https://element.eleme.cn/#/zh-CN/component/select
 * emit:
 */
export default {
  name: "WlVueSelect",
  data() {
    return {
      // value: "", // 
      empty: "00000000-0000-0000-0000-000000000000" // id
    };
  },
  model: {
    prop: "value", //v-model
    event: "change"
  },
  props: {
    // 
    data: {
      type: Array,
      default: () => {
        return [];
      }
    },
    //  label:"label", value: "value"
    props: Object,
    // ,
    showTotal: {
      type: Number,
      default: 1
    },
    // v-model
    value: [String, Array, Object],
    // value-key  value 
    valueKey: [String],
    // 
    multiple: {
      type: Boolean,
      default: false
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    size: String, // medium/small/mini
    // 
    clearable: {
      type: Boolean,
      default: false
    },
    //  0 
    multipleLimit: {
      type: Number,
      default: 0
    },
    // 
    placeholder: {
      type: String,
      default: ""
    },
    // 
    filterable: {
      type: Boolean,
      default: false
    },
    //  filterable 
    allowCreate: {
      type: Boolean,
      default: false
    },
    // 
    noCheckedClose: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    selectChange(val) {
      if (
        !this.multiple ||
        val.length === 0 ||
        this.showTotal >= this.selfData.length
      ) {
        this.$emit("change", val);
        if (val.length == 0 && this.noCheckedClose)
          this.$refs["wl-vue-select"].blur();
        return;
      }
      // 
      let _data = [];
      let _all_item_index = this.valueKey
        ? val.findIndex(item => item[this.selfProps.value] === this.empty)
        : val.findIndex(item => item === this.empty);
      if (_all_item_index === -1 && val.length === this.selfData.length) {
        _data = this.valueKey
          ? [
              {
                [this.selfProps.value]: this.empty,
                [this.selfProps.label]: ""
              }
            ]
          : [this.empty];
      } else {
        let arr_length_index = val.length - 1;
        if (_all_item_index === arr_length_index) {
          _data = [val[arr_length_index]];
        } else {
          _data = this.valueKey
            ? val.filter(item => item[this.selfProps.value] !== this.empty)
            : val.filter(item => item !== this.empty);
        }
      }
      this.$emit("change", _data);
    },
    // 
    whenDefaultCheckedAll(val) {
      if (this.selfData.length === 0) return;
      if (val.length === this.selfData.length) {
        let _data = this.valueKey
          ? [
              {
                [this.selfProps.value]: this.empty,
                [this.selfProps.label]: ""
              }
            ]
          : [this.empty];
        this.$emit("change", _data);
      }
    }
  },
  computed: {
    // 
    selfData() {
      return this.data;
    },
    // props
    selfProps() {
      return { label: "label", value: "value", ...this.props };
    }
  },
  watch: {
    // 
    value(val) {
      this.whenDefaultCheckedAll(val);
    }
  },
  created() {
    this.whenDefaultCheckedAll(this.value);
  }
};
</script>
